/*** base structure: page ***************************************************/

@import "../_metrics.scss";

///////////////////////////////////////////////////////////////////////
// 

.ui-page {
    // : padding 
    .ui-content {
        &.ui-screen-wide {
            padding: 0;
        }
        // : 
        &.ui-screen-full {
            position:   absolute;
            top:        0 !important;
            right:      0;
            bottom:     0;
            left:       0;
            padding:    0;
            margin:     0;
        }
    }
}

///////////////////////////////////////////////////////////////////////
// 

[class*=ui-platform-] {
    .ui-page {
        // fixed header 
        // 
        // fixed header ".ui-page-no-fix" 
        &:not(.ui-header-no-fix) {
            padding-top: $metrics-header-height !important;
        }
        // transition  
        // body  WebView native scrollbar  PageListView  
        // ".ui-page-no-fix" 
        &:not(.ui-page-no-fix) {
            height: initial !important;
            bottom: 0 !important;
        }

        // . (body )
        // jqm default  ".ui-screen-no-fix" 
        .ui-content:not(.ui-screen-no-fix) {
            position:   absolute;
            top:        $metrics-header-height + $metrics-common-border-width; // default header height + border
            right:      0;
            bottom:     0;
            left:       0;
        }
    }
}
