/*** transition : common settings ***************************************************/

@import "../_mixins.scss";

//  keyframe . 
@keyframes transition-no-effect {
    from {
        visibility: visible;
    }
    to {
        visibility: visible;
    }
}

// transition  page 
.ui-mobile-viewport-transitioning {
    .ui-page {
        position: fixed;
    }
}
