/* transition mixin */

// no-reaction transition
//
// out  animation jQuery Mobile 
//   1. in  animation  out  animationend 
//   2. out  animation ( duration) 
//      1000ms  Timeout  in  animation 
// 
// out  animation in  animation  1000ms 
// 
@mixin transition-no-reaction() {
    animation-name:     transition-no-effect;
    animation-duration: 1ms;
}
